import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            list1: null,
            choose: [],
            text: '',
            patient: this.$store.state.patient
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$post('PlatFormAPI/Category/QueryCategoryItem',{
                CategoryIds: [ 49 ]
            }).then(o => {
                o.forEach(v => {
                    if(v.CategoryId == 49){
                        this.list1 = v.Items
                    }
                })
                const info = this.$store.state.Health
                if(info){
                    this.text = info.DrugAllergy
                }
            })
        },
        send(){
            this.$post('PlatFormAPI/PatientAccount/SaveHealthInfo',{
                SaveType: 4,
                PatientId: this.patient.PatientId,
                DrugAllergy: this.text,
            }).then(o => {
            	this.$store.dispatch('pushIsFresh',true)
                this.$toast('保存成功')
                this.$router.back()
            })
        },
        push(i){
            const idx = this.choose.indexOf(i)
            if(idx == -1){
                this.text = ''
                this.choose.push(i)
                this.choose.forEach(v => {
                    this.text += v.CategoryItemName+";"
                })
            }
            else{
                this.text = ''
                this.choose.splice(idx,1)
                this.choose.forEach(v => {
                    this.text += v.CategoryItemName+";"
                })
            }
            
        }
    }
}



